.wp-block-nortic-plugin-breadcrumb .breadcrumb-nav span {
  color: var(--wp--preset--color--nortic-black);
  font-weight: 600;
}

.wp-block-nortic-plugin-breadcrumb .breadcrumb-nav .breadcrumb-item {
  color: var(--wp--preset--color--nortic-black);
  font-weight: 400;
}

.wp-block-nortic-plugin-breadcrumb
  .breadcrumb-nav
  .breadcrumb-item.current-breadcrumb-item,
.wp-block-nortic-plugin-breadcrumb
  .breadcrumb-nav
  .breadcrumb-item.current-breadcrumb-item:visited,
.wp-block-nortic-plugin-breadcrumb
  .breadcrumb-nav
  .breadcrumb-item.current-breadcrumb-item:where(:not(.wp-element-button)):visited {
  color: var(--wp--preset--color--nortic-blue);
  font-weight: 600;
}
